  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
body{
  scroll-behavior: smooth;
}
.main{
  width: 100%;
  height: auto;
  background-color: red;
}
.submain{
  max-width: 1080px;
  min-height: 600px;
  margin: 0 auto;
}
.headersec{
  width: 100%;
  height: 100vh;
  /* background-image: url(../img/banner.jpg); */
  background-size: cover;
  background-attachment: fixed;
}
